import http from "../http-common";

class ISOCDataService {
  getQualities(id_card,id_card_des) {
    return http.get('/isoc?id_card='+id_card+'&&id_card_des='+id_card_des);
  }
  getSuspectByIDCard(id) {
    return http.get(`/isoc/getidcard/`+id);
  }
  getIDSuspectQuality() {
    return http.get('/isoc/getidcardsuspect');
  }
  getQuality(id) {
    return http.get(`/isoc/`+id);
  }
  createQuality(data) {
    return http.post('/isoc', data);
  }
  updateQuality(id,data) {
    return http.put(`/isoc/${id}`, data);
  }
}

export default new ISOCDataService();