<script>
const customLabels = {
  first: "<<",
  last: ">>",
  previous: "<",
  next: ">",
};

import HomeIcon from "../../HomeIcon.vue";
import ISOCDataService from "../../../services/ISOCDataService";
import StatusDataService from "../../../services/StatusDataService.js";

export default {
  components: {
    HomeIcon,
  },
  data() {
    return {
      name: "HelloWorld",
      list: [],
      status: [],
      pageOfItems: [],
      date: "",
      options: {
        format: "DD/MM/YYYY",
        useCurrent: false,
      },
      location: "",
      latLong: "",
      customLabels,
      id_card: "",
      id_card_des: "",
    };
  },
  mounted() {
    var year = new Date().getFullYear()
    var start = year-2004
    var before = "-"+start+":+0"
    this.getCase();
    this.getstatus();
    picker_date(document.getElementById("my_date"), { year_range: before, });
  },
  methods: {
    back() {
      this.$router.push("/home");
    },
    onChangePage(pageOfItems) {
      // update page of items
      this.pageOfItems = pageOfItems;
    },
    getstatus() {
      StatusDataService.getStatus().then((res) => {
        this.status = res.data;
      });
    },

    convertdatetothai(datetimes) {
      if (datetimes) {
        const date = new Date(datetimes);
        const result = date.toLocaleDateString("th-TH", {
          year: "numeric",
          month: "short",
          day: "numeric",
        });
        return result;
      } else {
        return "-";
      }
    },
    getCase() {
      ISOCDataService.getQualities("", "").then((res) => {
        // console.log(res.data);
        this.list = res.data;
      });
    },
    hasHistory() {
      return window.history.length > 2;
    },
    search() {
      ISOCDataService.getQualities(this.id_card, this.id_card_des).then(
        (res) => {
          // console.log(res.data);
          this.list = res.data;
        }
      );
    },
  },

  computed: {
    currentUser() {
      return this.$store.state.auth.user;
    },
  },
};
</script>

