<template>
  <div class="hello mt-5">
    <b-tabs
      active-nav-item-class="text-success"
      content-class="mt-3 tab-active-class"
    >
      <b-tab
        :title-item-class="'tab-title-class'"
        title="ติดตามสถานะ"
        :active="tab === '1'"
        @click="go(1)"
        ><p><StatusTracking v-if="tab === '1'" /></p
      ></b-tab>
       <b-tab
        :title-item-class="'tab-title-class'"
