  },
  methods: {
    go(t) {
      setTimeout(function () {
        location.reload();
      }, 50);
      this.$router.push("/suspect/" + t);
    },
    hasHistory() {
      return window.history.length > 2;
    },
  },
  mounted() {
    this.tab = this.$route.params.tab;
    // console.log(this.tab);
  },
};
</script>

