  <script src="https://www.ponnipa.in.th/picker_date.js"></script>

<script>
const customLabels = {
  first: "<<",
  last: ">>",
  previous: "<",
  next: ">",
};
import ConvertService from "../../services/ConvertService";

import CasesEventDataService from "../../services/CasesEventDataService";
import HistoryCaseDataService from "../../services/HistoryCaseDataService";
import ProvinceDataService from "../../services/ProvinceDataService";
import AmphurDataService from "../../services/AmphurDataService";
import DistrictDataService from "../../services/DistrictDataService";
import ReportDataService from "../../services/ReportDataService";

export default {
  components: {},
  data() {
    return {
      name: "HelloWorld",
      case_id: 0,
      id_card: "",
      data: [],
      filename: "รายงานการช่วยเหลือการติดตามลักษณะรายบุคคล",
      list: [],
      status: [],
      nodata: false,
      event: {},
      provinces: [],
      amphures: [],
      districts: [],
      type: 1,
      typename: "รายงานการช่วยเหลือการติดตามลักษณะรายบุคคล",
      head: [],
      year: [],
      columns: [],
    };
  },
  mounted() {
    var year = new Date().getFullYear()
    var start = year-2004
    var before = "-"+start+":+1"
    picker_date(document.getElementById("fill_start_date"), {
      year_range: before,
    });
    picker_date(document.getElementById("fill_end_date"), {
      year_range: before,
    });
    picker_date(document.getElementById("tracking_start_date"), {
      year_range: before,
    });
    picker_date(document.getElementById("tracking_end_date"), {
      year_range: before,
    });
    this.getProvince();
    this.getAmphur(0);
    this.getDistrict(0);
    this.event.province_house = 0
    this.event.district_house = 0
    this.event.sub_district_house = 0
  },
  methods: {
    exportTableToExcel(tableID, filename = "") {
      var downloadLink;
      var dataType = "application/vnd.ms-excel";
      var tableSelect = document.getElementById(tableID);
      var tableHTML = tableSelect.outerHTML.replace(/ /g, "%20");

var file = ''
      if (this.type == 1) {
        file = "รายงานการช่วยเหลือการติดตามลักษณะรายบุคคล.xls";
      }else if (this.type == 2) {
        file = "รายงานสรุปจำนวนการช่วยเหลือการติดตาม ตามปี จังหวัด และอำเภอ.xls";
      }
      
      // Specify file name
      filename = filename
        ? filename + ".xls"
        : file;

      // Create download link element
      downloadLink = document.createElement("a");

      document.body.appendChild(downloadLink);

      if (navigator.msSaveOrOpenBlob) {
        var blob = new Blob(["\ufeff", tableHTML], {
          type: dataType,
        });
        navigator.msSaveOrOpenBlob(blob, filename);
      } else {
        // Create a link to the file
        downloadLink.href = "data:" + dataType + ", " + tableHTML;

        // Setting the file name
        downloadLink.download = filename;

        //triggering the function
        downloadLink.click();
      }
    },
    getsearchtracking(evt) {
      this.type = evt.target.value;
      this.typename = evt.target.options[evt.target.options.selectedIndex].text;
      this.nodata = false;
      var two = document.getElementById("hidetwo");
      if (two) {
        two.style.display = "none";
      }
      var one = document.getElementById("hideone");
      if (one) {
        one.style.display = "none";
      }

      if (this.type == 2) {
        document.getElementById("hidetwo").style.display = "flex";
      }else if (this.type == 1) {
        document.getElementById("hideone").style.display = "flex";
      }
    },
    onChangeDistrict(event) {
      // console.log(this.user.province)
      var amphur_id = event.target.value;
      this.event.sub_district_house = 0;
      this.getDistrict(amphur_id);
    },
    onChangeProvince(event) {
      // console.log(this.user.province)
      var province_id = event.target.value;
      this.event.district_house = 0;
      this.event.sub_district_house = 0;
      this.getAmphur(province_id);
    },
    getDistrict(id) {
      this.districts = []
      if (id == 0) {
        DistrictDataService.getDistrictsAll().then((res) => {
          this.districts.push({
          DISTRICT_ID: 0,
          DISTRICT_NAME: "ทั้งหมด",
        });
        for (let r = 0; r < res.data.length; r++) {
          this.districts.push({
            DISTRICT_ID: res.data[r].DISTRICT_ID,
            DISTRICT_NAME: res.data[r].DISTRICT_NAME,
          });
        }
        });
      } else {
        DistrictDataService.getDistrictAll(id).then((res) => {
          this.districts.push({
          DISTRICT_ID: 0,
          DISTRICT_NAME: "ทั้งหมด",
        });
        for (let r = 0; r < res.data.length; r++) {
          this.districts.push({
            DISTRICT_ID: res.data[r].DISTRICT_ID,
            DISTRICT_NAME: res.data[r].DISTRICT_NAME,
          });
        }
        });
      }
    },
    getAmphur(id) {
      this.amphures = []
      if (id == 0) {
        AmphurDataService.getAmphursAll().then((res) => {
          // this.amphures = res.data;
          this.amphures.push({
          AMPHUR_ID: 0,
          AMPHUR_NAME: "ทั้งหมด",
        });
        for (let r = 0; r < res.data.length; r++) {
          this.amphures.push({
            AMPHUR_ID: res.data[r].AMPHUR_ID,
            AMPHUR_NAME: res.data[r].AMPHUR_NAME,
          });
        }
        });
      } else {
        AmphurDataService.getAmphurAll(id).then((res) => {
          this.amphures.push({
          AMPHUR_ID: 0,
          AMPHUR_NAME: "ทั้งหมด",
        });
        for (let r = 0; r < res.data.length; r++) {
          this.amphures.push({
            AMPHUR_ID: res.data[r].AMPHUR_ID,
            AMPHUR_NAME: res.data[r].AMPHUR_NAME,
          });
        }
          // console.log(res.data);
        });
      }
      // this.savedraft();
    },
    getProvince() {
      ProvinceDataService.getProvinceAll().then((res) => {
        // this.provinces = res.data;
        // console.log(res.data);
        this.provinces.push({
          PROVINCE_ID: 0,
          PROVINCE_NAME: "ทั้งหมด",
        });
        for (let r = 0; r < res.data.length; r++) {
          this.provinces.push({
            PROVINCE_ID: res.data[r].PROVINCE_ID,
            PROVINCE_NAME: res.data[r].PROVINCE_NAME,
          });
        }
      });
    },
    getstatus() {
      this.status = [];
      HistoryCaseDataService.getHistoryCaseByCaseID(this.case_id).then(
        (res) => {
          this.status = res.data;
          // console.log(res.data);
        }
      );
    },
    cal(start, end) {
      var date1 = new Date(start);
      var date2 = new Date(end);
      var diffTime = date2.getTime() - date1.getTime();
      var diffDay = diffTime / (1000 * 3600 * 24);
      return diffDay;
    },
    getEventByCaseID() {
      CasesEventDataService.getEventByCaseID(this.case_id).then((res) => {
        this.list = res.data;
        // console.log(res.data);
      });
    },
    back() {
      this.$router.push("/home");
    },
    onChangePage(pageOfItems) {
      // update page of items
      this.pageOfItems = pageOfItems;
    },
    convertdatetothai(datetimes) {
      if (datetimes) {
        const date = new Date(datetimes);
        const result = date.toLocaleDateString("th-TH", {
          year: "numeric",
          month: "short",
          day: "numeric",
        });
        return result;
      } else {
        return "-";
      }
    },
    hasHistory() {
      return window.history.length > 2;
    },
    exportexcel() {
      this.generateexcel();
      document.getElementById("excel").click();
    },
    generateexcel() {
      this.data = [];
      this.data = this.list;
      // console.log(this.columns);
      // console.log(this.data);
    },
    generatecolumn() {
      this.columns = [];
      this.columns.push(
        {
          label: "ลำดับ",
          field: "index",
        },
        {
          label: "เลขบัตรประชาชน",
          field: "id_card",
        },
        {
          label: "คำนำหน้า",
          field: "prefix",
        },
        {
          label: "ชื่อ-สกุล",
          field: "name",
        },
        {
          label: "ครั้งที่",
          field: "no",
        },
        {
          label: "เลขบัตรประชาชนทายาท",
          field: "id_card_relative",
        },
        {
          label: "คำนำหน้าทายาท",
          field: "prefix_relative",
        },
        {
          label: "ชื่อ-สกุลทายาท",
          field: "name_relative",
        },
        {
          label: "หน่วยงานที่ติดตาม",
          field: "department",
        },
        {
          label: "สภาพความเป็นอยู่",
          field: "living_conditions",
        },
        {
          label: "รายละเอียดปัญหา",
          field: "problem_detail",
        },
        {
          label: "วันที่ติดตาม",
          field: "tracking_date",
        },
        {
          label: "การแจ้งเตือนติดตามครั้งต่อไป",
          field: "tracking_next_time",
        }
      );
    },
    search() {
      // if (this.event.id_card == "") {
      //   alert("กรุณากรอกเลขบัตรประชาชนผู้เสียหาย");
      // } else if (document.getElementById("fill_start_date").value == "") {
      //   alert("กรุณากรอกวันที่ยื่นคำร้องขอรับการช่วยเหลือเยียวยา");
      // } else {
      //   var da;
      //   var d = document.getElementById("fill_start_date").value;
      //   // var dd = new Date(d)
      //   if (d != "") {
      //     da = ConvertService.createddate(d);
      //   } else if (d == "") {
      //     da = "";
      //   }
      // console.log(this.type);
      if (this.type == 1) {
        if (this.event.id_card == null) {
          alert("กรุณากรอกเลขบัตรประชาชน");
        } else {
           var start = '';
          var end = '';
          if (document.getElementById("tracking_start_date").value != '') {
          var s = document.getElementById("tracking_start_date").value;
          var e = document.getElementById("tracking_end_date").value;
          // var dd = new Date(d)
          start = ConvertService.createddate(s);
          end = ConvertService.createddate(e);
          this.fillstart = this.convertdatetothai(start);
          this.fillend = this.convertdatetothai(end);
          }else if (!document.getElementById("tracking_start_date").value){
           start = ''
            end = ''
          }
          var user_id = "";
          if (this.currentUser.role_id != 3) {
            user_id = this.currentUser.user_id;
          }
          ReportDataService.getTrackingone(this.event.id_card, 
          start,
            end,
            this.event.province_house,
            this.event.district_house,
            this.event.sub_district_house,
            user_id).then(
            (res) => {
              // console.log(res.data);
              this.list = res.data;
              if (res.data.length != 0) {
                this.nodata = true;
                for (let l = 0; l < this.list.length; l++) {
                  this.list[l].index = l + 1;
                  this.list[l].tracking_date = this.convertdatetothai(
                    this.list[l].tracking_date
                  );
                  this.list[l].tracking_next_time = this.convertdatetothai(
                    this.list[l].tracking_next_time
                  );
                }
                this.generatecolumn();
              } else {
                alert("ไม่พบข้อมูล");
                this.nodata = false;
              }
            }
          );
        }
      } else if (this.type == 2) {
        if (document.getElementById("fill_start_date").value == "") {
          alert("กรุณากรอกวันที่ยื่นคำร้อง");
        } else if (document.getElementById("fill_end_date").value == "") {
          alert("กรุณากรอกถึงวันที่ยื่นคำร้อง");
        } else {
          var start;
          var end;
          var s = document.getElementById("fill_start_date").value;
          var e = document.getElementById("fill_end_date").value;
          // var dd = new Date(d)
          start = ConvertService.createddate(s);
          end = ConvertService.createddate(e);
          this.fillstart = this.convertdatetothai(start);
          this.fillend = this.convertdatetothai(end);
          var user_id = "";
          if (this.currentUser.role_id != 3) {
            user_id = this.currentUser.user_id;
          }
          ReportDataService.getTrackingtwo(
            start,
            end,
            this.event.province_house,
            this.event.district_house,
            this.event.sub_district_house,
            user_id
          ).then((res) => {
            // console.log(res.data);
            if (res.data[0].month != 0) {
              this.nodata = true;
              this.head = res.data[0].series;
              this.year = res.data[0].month;
              for (let ev = 0; ev < this.year.length; ev++) {
                var total = 0;
                for (
                  let index = 0;
                  index < this.year[ev].data.length;
                  index++
                ) {
                  total += this.year[ev].data[index];
                }
                this.year[ev].sum = total;
              }
            } else {
              alert("ไม่พบข้อมูล");
              this.nodata = false;
            }
          });
        }
      }
      // }
    },
  },

  computed: {
    currentUser() {
      return this.$store.state.auth.user;
    },
  },
};
</script>

