<template>
  <div class="hello">
        <HomeIcon />
    <div class="mt-5">
    <h3 style="text-align: center">{{ header }}</h3>
      <div class="row">
        <div class="col-10"></div>
        <div class="col-2" style="text-align: right">
          <button
            type="button"
            class="btn btn-block btn-success"
            data-toggle="modal"
            data-target="#addModal"
            @click="sendInfo('add', 0)"
          >
            เพิ่ม
          </button>
        </div>

        <!-- Modal -->
        <div
          class="modal fade"
          id="addModal"
          tabindex="-1"
          role="dialog"
          aria-labelledby="addModalLabel"
          aria-hidden="true"
        >
          <div class="modal-dialog" role="document">
            <div class="modal-content">
              <div class="modal-header">
                <button
                  type="button"
                  class="close"
                  data-dismiss="modal"
                  aria-label="Close"
                >
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div class="modal-body">
                <!-- Role -->
                <h4 style="text-align: center; font-weight: bold">
                  {{ title }}
                </h4>
                <div class="row mt-5">
                  <div class="col-12">
                    <div class="form-group" style="text-align: left">
                      <label for="header"
                        >{{ header }}<span style="color: red"> *</span></label
                      >
                      <input
                        type="text"
                        class="form-control"
                        id="name"
                        v-model="data.name"
                        :placeholder="'กรุณากรอก' + header"
                      />
                    </div>
                  </div>
                  <div class="col-2" style="text-align: left">
                    <label for="status"
                      >สถานะ<span style="color: red"> *</span></label
                    >
                  </div>
                  <div class="col-3" style="text-align: left">
                    <div class="form-check">
                      <input
                        class="form-check-input"
                        id="active"
                        value="Active"
                        type="radio"
                        name="active"
                        v-model="ischeck"
                      />
                      <label class="form-check-label" for="active"
                        >ใช้งานอยู่</label
                      >
                    </div>
                  </div>
                  <div class="col-7">
                    <div class="form-check" style="text-align: left">
                      <input
                        class="form-check-input"
                        id="disactive"
                        value="Disactive"
                        type="radio"
                        name="active"
                        v-model="ischeck"
                      />
                      <label class="form-check-label" for="disactive"
                        >เลิกใช้งาน</label
                      >
                    </div>
                  </div>
                </div>
              </div>
              <div class="bd-example mb-5">
                <button
                  type="button"
                  style="margin-right: 20px; width: 150px"
                  class="btn btn-success"
                  @click="save()"
                >
                  บันทึก
                </button>
                <button
                  type="button"
                  id="closed"
                  class="btn btn-danger"
                  style="margin-right: 20px; width: 150px"
                  data-dismiss="modal"
                >
                  ยกเลิก
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="mt-5 mb-5" v-if="list.length == 0">
        <h4 style="text-align: center">ไม่พบข้อมูลในระบบ</h4>
      </div>

      <div v-else class="row mt-5">
        <div class="col-1"></div>
        <div class="col-7">ประเภทการช่วยเหลือ</div>
        <div class="col-2">สถานะ</div>
        <div class="col-2" style="text-align: right">จัดการ</div>
      </div>
      <div class="row card mt-2" v-for="(l, index) in list" :key="l.no">
        <div class="card-body p-0">
          <div class="row">
            <div class="col-1">{{ index + 1 }}</div>
            <div class="col-7">{{ l.name }}</div>
            <div class="col-3" v-if="l.is_active == true">ใช้งานอยู่</div>
            <div class="col-3" v-if="l.is_active == false">เลิกใช้งาน</div>
            <div class="col-1" style="text-align: right">
              <a
                data-toggle="modal"
                data-target="#addModal"
                href="#"
                @click="sendInfo('edit', l.event_type_id)"
                ><i class="fas fa-pencil-alt"></i
              ></a>
              <router-link
                style="margin-left: 5px"
                :to="'privilege/' + l.event_type_id"
                ><i class="fas fa-link"></i
              ></router-link>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col text-center">
        <button
          class="btn btn-warning"
          style="color: white"
          @click="hasHistory() ? $router.go(-1) : $router.push('/')"
        >
          ย้อนกลับ
        </button>
      </div>
    </div>
  </div>
</template>
<script>
import EventTypesDataService from "../../services/EventTypesDataService";
import HomeIcon from "../HomeIcon.vue";

export default {
  components: {
    HomeIcon,
  },

  data() {
    return {
      name: "HelloWorld",
      list: [],
      title: "",
      data: [],
      ischeck: "Active",
      header: "ประเภทการช่วยเหลือตามระเบียบ กพต.",
    };
  },
  mounted() {
    this.getlist();
  },
  methods: {
    getlist() {
      EventTypesDataService.getEventTypes().then((res) => {
        this.list = res.data;
        // console.log(res.data);
      });
    },
    getid(id) {
      EventTypesDataService.getEventType(id).then((res) => {
        this.data = res.data;
        // console.log(res.data);
        if (this.data.is_active == true) {
          this.ischeck = "Active";
        } else {
          this.ischeck = "Disactive";
        }
      });
    },
    sendInfo(item, id) {
      // console.log(item, id);
      if (item == "add") {
        this.title = "เพิ่ม" + this.header;
        this.ischeck == "Active";
        this.data = []
      } else {
        this.title = "แก้ไข" + this.header;
        this.getid(id);
      }
    },
    save() {
      if (this.ischeck == "Active") {
        this.data.is_active = true;
      } else {
        this.data.is_active = false;
      }
      var listdata = {
        name: this.data.name,
        is_active: this.data.is_active,
        created_by: this.currentUser.user_id,
        updated_by: this.currentUser.user_id,
      };

      // console.log(listdata);
      if (this.data.name == "" || this.data.name == null) {
        alert("กรุณากรอกข้อมูลให้ครบ");
      }else{
      if (this.data.event_type_id) {
        EventTypesDataService.updateEventType(this.data.event_type_id, listdata)
          .then(() => {
            // console.log(response.data);
            this.data = [];
            this.ischeck = true;
            this.getlist();
            document.getElementById("closed").click();
          })
          .catch((e) => {
            console.log(e);
          });
      } else {
        EventTypesDataService.createEventType(listdata)
          .then(() => {
            // console.log(response.data);
            this.data = [];
            this.ischeck = true;
            this.getlist();
            document.getElementById("closed").click();
          })
          .catch((e) => {
            console.log(e);
          });
      }
      }
    },
    hasHistory() {
      return window.history.length > 2;
    },
  },
  computed: {
    currentUser() {
      return this.$store.state.auth.user;
    },
  },
};
</script>

