<template>
  <div class="hello">
    <HomeIcon />
    <div class="mt-5">
      <div class="row mt-5">
        <h4 style="text-align: center">
          ข้อมูลรอการอนุมัติจ่ายเงิน เลขหนังสืออ้างอิง {{ ref_no }}
        </h4>
      </div>
      <div class="mt-5">
        <div class="row">
          <div class="col-md-12">
            <h4 style="text-align: left" class="mb-5">
              รายการข้อมูลการยื่นเรื่องขออนุมัติเบิกเงิน
            </h4>
          </div>
        </div>
      </div>
      <ViewPayment />

      <div class="row card mt-5">
        <div class="card-body p-0 mt-3">
          <h4 style="text-align: center">
            ข้อมูลรอการอนุมัติจ่ายเงิน เลขหนังสืออ้างอิง {{ ref_no }}
          </h4>
          <div class="row">
            <div class="col-md-2"></div>
            <div class="col-md-4">
              <div class="form-group">
                <label for="exampleInputPassword1">วันที่ลงนาม</label>
                <input id="signing_date" class="form-control" />
              </div>
            </div>
            <div class="col-md-4">
              <div class="form-group">
                <label for="exampleInputPassword1">รายละเอียดเพิ่มเติม</label>
                <input
                  v-model="remark_signing"
                  type="text"
                  class="form-control"
                  placeholder="รายละเอียดเพิ่มเติม"
                />
              </div>
            </div>
          </div>
        </div>
        <div class="row">
          <div class="col-md-12 text-center">
            <button
              class="btn btn-warning"
              style="color: white; width: 200px"
              @click="back()"
            >
              ย้อนกลับ</button
            >&nbsp;
            <button
              @click="save()"
              class="btn btn-success"
              style="color: white; width: 200px"
              id="saveimage"
            >
              บันทึก
            </button>
          </div>
        </div>
      </div>
