<template>
  <div>
    <vue-advanced-chat
      height="calc(100vh - 20px)"
      :current-user-id="currentUserId"
      :rooms="JSON.stringify(rooms)"
      :rooms-loaded="true"
      show-audio="false"
      show-add-room="false"
      show-reaction-emojis="false"
      show-emojis="false"
      show-search="false"
      accepted-files="video/*,image/*"
      :messages="JSON.stringify(messages)"
      :messages-loaded="messagesLoaded"
      @send-message="sendMessage($event.detail[0])"
      @fetch-messages="fetchMessages($event.detail[0])"
      :message-actions="messageActions"
      :text-messages="
        JSON.stringify({
          TYPE_MESSAGE: 'ถามคำถาม...',
          CONVERSATION_STARTED: '',
          NEW_MESSAGES: '',
          MESSAGES_EMPTY: '',
        })
      "
    />
  </div>
</template>

<script>
import { register } from "vue-advanced-chat";
import ChatDataService from "../../services/ChatDataService.js";
import axios from "axios";
import ConvertService from "../../services/ConvertService.js";


register();

export default {
  data() {
    return {
      messageActions: [],
      currentUserId: "4321",
      rooms: [
        {
          roomId: "1",
          roomName: "เจ้าหน้าที่ ศอ.บต.",
          avatar: "https://sbpac.ponnipa.in.th/logo.jpg",
        },
      ],
      messages: [],
      messagesLoaded: false,
      roomId: 0,
      senderId: 0,
      timer: null,
      roomcount: 0,
      messageslist: [],
    };
  },

  methods: {
    beforeDestroy() {
      clearInterval(this.timer);
    },
    convertdatetothai(datetimes) {
      if (datetimes) {
        const date = new Date(datetimes);
        const result = date.toLocaleDateString("th-TH", {
          year: "numeric",
          month: "short",
          day: "numeric",
        });
        return result;
      } else {
        return "-";
      }
    },
    getRooms() {
      ChatDataService.getRooms().then((res) => {
        this.roomcount = res.data.length;
        // console.log(this.roomcount);
      });
    },
    fetchMessages({ options = {} }) {
      // console.log(room.roomId);
      // console.log(this.messages);
      // console.log(this.messages.length);
      setTimeout(() => {
        if (options.reset) {
          if (this.roomId !== 0) {
            this.timer = setInterval(() => {
              this.getmessageList();
              // console.log(this.messageslist.length);
              // console.log(this.messages.length);
              if (this.messageslist.length !== this.messages.length) {
                this.messages = []
                this.getmessage()
              }
            }, 1000);
          }  
          // console.log(this.messages);
          // this.messages = this.addMessages(true);
          
        this.messagesLoaded = true;
        } else {
          this.getmessage();
          this.messagesLoaded = true;
        }
      });
    },

    addMessages(reset) {
      const messages = [];

      for (let i = 0; i < 30; i++) {
        messages.push({
          _id: reset ? i : this.messages.length + i,
          content: `message ${i + 1}`,
          senderId: "4321",
          username: "John Doe",
          date: "13 November",
          timestamp: "10:20",
          seen: true,
          avatar: "https://sbpac.ponnipa.in.th/logo.jpg",
        });
      }
      messages.push({
        _id: 123,
        content: "test",
        senderId: "1234",
        username: "John Snow",
        date: "5 กันยายน 2565",
        timestamp: "10:20",
        seen: true,
        avatar: "https://sbpac.ponnipa.in.th/user.png",
        files: [
          {
            name: "My Replied File",
            size: 67351,
            type: "image/png",
            audio: true,
            duration: 14.4,
            url: "blob:http://localhost:8081/a4aa5820-8598-473b-a98b-ac2a3cb27843",
            // preview:
            //   "https://i.picsum.photos/id/118/1500/1000.jpg?hmac=jumRsHTbfg0frrVd5Xw1187nwNcZCqJbqrdlXpPAIjw",
          },
        ],
      });
      messages.push({
        _id: 12356,
        content: "วิดีโอ",
        senderId: "1234",
        username: "John Snow",
        date: "5 กันยายน 2565",
        timestamp: "10:20",
        seen: true,
        avatar: "https://sbpac.ponnipa.in.th/user.png",
        files: [
          {
            name: "123",
            size: 67351,
            type: "mp4",
            audio: true,
            url: "http://commondatastorage.googleapis.com/gtv-videos-bucket/sample/ElephantsDream.mp4",
          },
          {
            name: "123",
            size: 67351,
            type: "mp4",
            audio: true,
            url: "http://commondatastorage.googleapis.com/gtv-videos-bucket/sample/ElephantsDream.mp4",
          },
        ],
      });

      return messages;
    },

    generateguid() {
      let s4 = () => {
        return Math.floor((1 + Math.random()) * 0x10000)
          .toString(16)
          .substring(1);
      };
      //return id of format 'aaaaaaaa'-'aaaa'-'aaaa'-'aaaa'-'aaaaaaaaaaaa'
      return (
        s4() +
        s4() +
        "-" +
        s4() +
        "-" +
        s4() +
        "-" +
        s4() +
        "-" +
        s4() +
        s4() +
        s4()
      );
    },
    getMessageFile(data) {
      if (this.messages.length != data.length) {
        this.messages = [];
      }
      this.messages = data
      this.messages.forEach((ms,index) => {
        // console.log(ms);
        // console.log(index);
        ChatDataService.getMessageFile(ms._id).then((res) => {
          // console.log(res.data);
          if (res.data.length > 0) {
            var list = [];
            res.data.forEach((value) => {
              list.push({
                name: value.name,
                size: value.size,
                type: value.type,
                audio: true,
                url: value.url,
              });
            });
          }
          var t = ms.timestamp;
          t = t.split(":");
          this.messages[index].timestamp = t[0] + "." + t[1] + " น."
          this.messages[index].date = this.convertdatetothai(ms.date)
          this.messages[index].files = list
        });
      });
    },
    getmessage() {
      ChatDataService.getMessage(this.roomId).then((res) => {
        if (res.data.length == 0) {
          this.messages.push({
            _id:1,
            content:
              "สวัสดีค่ะ ศอ.บต. ยินดีให้บริการ ติดต่อสอบถาม ข้อมูลได้เลยค่ะ",
            senderId: "7",
            username: "support",
            avatar: "https://sbpac.ponnipa.in.th/logo.jpg",
          });
        }else{
          this.getMessageFile(res.data);
        }
        // console.log(this.messages);
        
      });
    },
    onUploadFile(files) {
      const formData = new FormData();
      formData.append("file", files); // appending file
      //  sending file to the backend
      // var http = "https://api-ri.ponnipa.in.th/upload";
      var http = "http://localhost:8080/upload";
      axios
        .post(http, formData)
        .then(() => {
          // console.log(res.data);
        })
        .catch((err) => {
          console.log(err);
        });
    },
     getnewfile(localUrl, name) {
      axios({
        method: "get",
        url: localUrl, // blob url eg. blob:http://127.0.0.1:8000/e89c5d87-a634-4540-974c-30dc476825cc
        responseType: "blob",
      }).then(function (response) {
        var reader = new FileReader();
        reader.readAsDataURL(response.data);
        reader.onloadend = function () {
          var base64data = reader.result;
          // console.log(base64data);
          var arr = base64data.split(","),
            mime = arr[0].match(/:(.*?);/)[1],
            bstr = atob(arr[1]),
            n = bstr.length,
            u8arr = new Uint8Array(n);

          while (n--) {
            u8arr[n] = bstr.charCodeAt(n);
          }

          var data = new File([u8arr], name, { type: mime });
          const formData = new FormData();
          formData.append("file", data); // appending file
          //  sending file to the backend
          // var http = "https://api-ri.ponnipa.in.th/upload";
          
    var link = ConvertService.gethttp()
    // console.log(link);
          var http = link+"/upload?name="+name;
          axios
            .post(http, formData)
            .then(() => {
              // console.log(res.data);
            })
            .catch((err) => {
              console.log(err);
            });
        };
      });
    },
    sendMessage(message) {
      if (this.roomId == 0) {
        var roomid = (parseInt(this.roomcount) + 1)
        // console.log(roomid);
        var room = {
          roomName: "ผู้รับบริการที่ " + roomid,
          avatar: "https://sbpac.ponnipa.in.th/user.png",
          unreadCount:1
        };
        ChatDataService.createRoom(room).then((res) => {
          this.roomId = res.data.id;
          localStorage.setItem(
            "room",
            JSON.stringify({ roomId: this.roomId, senderId: this.senderId })
          );
          // console.log(this.roomId);

          var support = {
            room_id: this.roomId,
            content: `สวัสดีค่ะ ศอ.บต. ยินดีให้บริการ ติดต่อสอบถาม ข้อมูลได้เลยค่ะ`,
            senderId: "7",
            username: "support",
            seen: false,
            avatar: "https://sbpac.ponnipa.in.th/logo.jpg",
          };
          ChatDataService.createMessage(support).then(() => {
            // console.log(res.data);
            var data = {
            room_id: this.roomId,
            content: message.content,
            senderId: this.senderId,
            username: "",
            seen: false,
            avatar: "https://sbpac.ponnipa.in.th/user.png",
          };
          // console.log(data);
          ChatDataService.createMessage(data).then((res) => {
            if (message.files) {
          for (let f = 0; f < message.files.length; f++) {
            this.getnewfile(
              message.files[f].localUrl,
              message.files[f].name + "." + message.files[f].extension
            );
            // console.log(message.files[f]);
            var urlpath =
              "https://api-sbpacdb.ponnipa.in.th/uploads/" +
              message.files[f].name +
              "." +
              message.files[f].extension;
            var file = {
              message_id: res.data.id,
              name: message.files[f].name,
              size: message.files[f].size,
              type: message.files[f].extension,
              audio: true,
              url: urlpath,
            };
            // console.log(file);
            ChatDataService.createMessageFile(file).then(() => {
              // console.log(res.data);
            });
          }
          }
          });          
          location.reload();
        });
          });
          
      } else {
        ChatDataService.getRoom(this.roomId).then((res)=>{
          var roomdata = res.data
 var room = {
          unreadCount:parseInt(roomdata.unreadCount)+1
        };
        ChatDataService.updateRoom(this.roomId,room).then(() => {
          
        var data = {
          room_id: this.roomId,
          content: message.content,
          senderId: this.senderId,
          username: "",
          seen: false,
          avatar: "https://sbpac.ponnipa.in.th/user.png",
        };
        ChatDataService.createMessage(data).then((res) => {
          if (message.files) {
          for (let f = 0; f < message.files.length; f++) {
            this.getnewfile(
              message.files[f].localUrl,
              message.files[f].name + "." + message.files[f].extension
            );
            // console.log(message.files[f]);
            var urlpath =
              "https://api-sbpacdb.ponnipa.in.th/uploads/" +
              message.files[f].name +
              "." +
              message.files[f].extension;
            var file = {
              message_id: res.data.id,
              name: message.files[f].name,
              size: message.files[f].size,
              type: message.files[f].extension,
              audio: true,
              url: urlpath,
            };
            // console.log(file);
            ChatDataService.createMessageFile(file).then(() => {
              // console.log(res.data);
            });
          }
          }
        });
        });
      
        })
       
      }
    },

    addNewMessage() {
      setTimeout(() => {
        this.messages = [
          ...this.messages,
          {
            _id: this.messages.length,
            content: "NEW MESSAGE",
            senderId: "1234",
            timestamp: new Date().toString().substring(16, 21),
            date: new Date().toDateString(),
          },
        ];
      }, 2000);
    },
    getmessageList() {
      ChatDataService.getMessage(this.roomId).then((res) => {
        // console.log(res.data);
        this.getMessageFileList(res.data);
      });
    },
    getMessageFileList(data) {
      if (this.messages.length != data.length) {
        this.messageslist = [];
      this.messageslist = data
      this.messageslist.forEach((ms,index) => {
        // console.log(ms);
        // console.log(index);
        ChatDataService.getMessageFile(ms._id).then((res) => {
          // console.log(res.data);
          if (res.data.length > 0) {
            var list = [];
            res.data.forEach((value) => {
              list.push({
                name: value.name,
                size: value.size,
                type: value.type,
                audio: true,
                url: value.url,
              });
            });
          }
          var t = ms.timestamp;
          t = t.split(":");
          this.messageslist[index].timestamp = t[0] + "." + t[1] + " น."
          this.messageslist[index].date = this.convertdatetothai(ms.date)
          this.messageslist[index].files = list
        });
      });
    
      }}
  },
  mounted() {
    // localStorage.removeItem("room");
    var room = localStorage.getItem("room");
    console.log(room);
    if (room) {
      room = JSON.parse(room);
      this.roomId = room.roomId;
      this.senderId = room.senderId;
      this.currentUserId = this.senderId;
    } else {
      this.getRooms();
      this.getmessage()
      this.senderId = this.generateguid();
      this.currentUserId = this.senderId;        
    }
    // setTimeout(() => {
    //   this.getmessage();
    // }, 500);
    // document.getElementById("roomTextarea").placeholder="ถามคำถาม...";
  },
};
</script>

<style >
.vac-loader-wrapper #vac-circle {
  /* display: none !important; */
}
</style>
