          this.list[l].village_no_house = "-";
        }
        if (!this.list[l].road_house) {
          this.list[l].road_house = "-";
        }
        if (!this.list[l].lane_house) {
          this.list[l].lane_house = "-";
        }
        if (!this.list[l].village_house) {
          this.list[l].village_house = "-";
        }
