            this.list[l].province_house = "";
            this.list[l].postal_code_house = "";
            this.list[l].birthday = "";
            this.list[l].filing_date = "";
            this.list[l].phone = "";
            this.list[l].gender = "";
            this.list[l].suspect_types = "";
            this.list[l].career = "";
            this.list[l].income = "";
            this.list[l].nationality = "";
            this.list[l].no_current = "";
