              // this.list[l].prefix =  "รวมเป็นเวลา "+ this.list[l].sumday+" วัน เป็นเงิน " +this.list[l].sumamount.toLocaleString() + " บาท"
            }
          } else {
            no = no + 1;
            this.list[l].no = no;
          }
        } else {
          no = no + 1;
          this.list[l].no = no;
        }
        if (this.list[l].village_no_house == null) {
          this.list[l].village_no_house = "-";
        }
      }
