  <script src="https://www.ponnipa.in.th/picker_date.js"></script>

<script>
import HomeIcon from "../HomeIcon.vue";
import TrackingDataService from "../../services/TrackingDataService";
import StatusDataService from "../../services/StatusDataService.js";
import ConvertService from "../../services/ConvertService";

const customLabels = {
    first: '<<',
    last: '>>',
    previous: '<',
    next: '>'
};

export default {
  components: {
    HomeIcon,
  },
  data() {
    return {
      name: "HelloWorld",
      list: [],
      status: [],
      pageOfItems: [],
      date: "",
      options: {
        format: "DD/MM/YYYY",
        useCurrent: false,
      },
      location: "",
      latLong: "",
      customLabels,
      department:'',
      id_card:''
    };
  },
  mounted() {
     var year = new Date().getFullYear()
    var start = year-2004
    var before = "-"+start+":+0"
    this.getCase();
    this.getstatus();
    picker_date(document.getElementById("my_date"), { year_range: before, });
  },
  methods: {
    back(){
      this.$router.push("/home");
    },
    onChangePage(pageOfItems) {
      // update page of items
      this.pageOfItems = pageOfItems;
    },
    getstatus() {
      StatusDataService.getStatus().then((res) => {
        this.status = res.data;
      });
    },

    convertdatetothai(datetimes) {
      if (datetimes) {
        const date = new Date(datetimes);
        const result = date.toLocaleDateString("th-TH", {
          year: "numeric",
          month: "short",
          day: "numeric",
        });
        return result;
      } else {
        return "-";
      }
    }, 
    getCase() {
      TrackingDataService.getTrackings('','','').then(
        (res) => {
          // console.log(res.data);
          this.list = res.data;
        }
      );
    },
    hasHistory() {
      return window.history.length > 2;
    },
    search(){ 
      var da;
      var d = document.getElementById("my_date").value
      if (d != "") {
        da = ConvertService.createddate(d);
      } else if (d == "") {
        da = "";
      }
      // console.log(da);
      TrackingDataService.getTrackings(this.id_card,da,this.department).then(
        (res) => {
          // console.log(res.data);
          this.list = res.data;
          // console.log(d);
        }
      );
    }
  },

  computed: {
    currentUser() {
      return this.$store.state.auth.user;
    },
  },
};
</script>

