           if (this.list[li].no != '') {
             var save ={
        status_id:9,
        updated_by:this.currentUser.user_id
      }
      CasesDataService.updateCheck(this.list[li].case_id,save).then(() => {
        // var now = ConvertService.getdate(Date.now());
