      this.event.district_house = 0;
      this.event.sub_district_house = 0;
      this.getAmphur(province_id);
    },
    getDistrict(id) {
      this.districts = []
      if (id == 0) {
        DistrictDataService.getDistrictsAll().then((res) => {
          this.districts.push({
          DISTRICT_ID: 0,
          DISTRICT_NAME: "ทั้งหมด",
        });
