    onlyNumber($event) {
      if (this.suspect.id_card != "") {
        let keyCode = $event.keyCode ? $event.keyCode : $event.which;
        if (keyCode < 48 || keyCode > 57 || this.suspect.id_card.length == 13) {
          // 46 is dot
          $event.preventDefault();
        }
      }
