import http from "../http-common";

class MOPHDataService {
  getQualities(id_card,id_card_des) {
    return http.get('/moph?id_card='+id_card+'&&id_card_des='+id_card_des);
  }
  getSuspectByIDCard(id) {
    return http.get(`/moph/getidcard/`+id);
  }
  getIDSuspectQuality() {
    return http.get('/moph/getidcardsuspect');
  }
  getQuality(id) {
    return http.get(`/moph/`+id);
  }
  createQuality(data) {
    return http.post('/moph', data);
  }
  updateQuality(id,data) {
    return http.put(`/moph/${id}`, data);
  }
}

export default new MOPHDataService();