import http from "../http-common";

class DisasterDetailDataService {
  getQualities(id_card,id_card_des) {
    return http.get('/disaster_detail?id_card='+id_card+'&&id_card_des='+id_card_des);
  }
  getIDSuspectQuality() {
    return http.get('/disaster_detail/getidcardsuspect');
  }
  getQuality(id,type) {
    return http.get(`/disaster_detail/`+id+'/'+type);
  }
  getDetailQuality(id,type) {
    return http.get(`/disaster_detail/getdetail/`+id+'/'+type);
  }  
  createQuality(data) {
    return http.post('/disaster_detail', data);
  }
  updateQuality(id,data) {
    return http.put(`/disaster_detail/${id}`, data);
  }
}

export default new DisasterDetailDataService();