              this.nodata = true;
              // console.log(res.data);
              this.getprivilsix();
              this.managecontentsix();
            } else {
              alert("ไม่พบข้อมูล");
              this.nodata = false;
            }
          });
        }
      }
    },
  },

  computed: {
    currentUser() {
      return this.$store.state.auth.user;
    },
  },
};
</script>

