import http from "../http-common";

class MOEDetailDataService {
  getQualities(id_card,id_card_des) {
    return http.get('/moe_detail?id_card='+id_card+'&&id_card_des='+id_card_des);
  }
  getIDSuspectQuality() {
    return http.get('/moe_detail/getidcardsuspect');
  }
  getQuality(id,type) {
    return http.get(`/moe_detail/`+id+'/'+type);
  }
  getDetailQuality(id,type) {
    return http.get(`/moe_detail/getdetail/`+id+'/'+type);
  }  
  createQuality(data) {
    return http.post('/moe_detail', data);
  }
  updateQuality(id,data) {
    return http.put(`/moe_detail/${id}`, data);
  }
}

export default new MOEDetailDataService();