import http from "../http-common";

class MSocietyDataService {
  getQualities(id_card,id_card_des) {
    return http.get('/m_society?id_card='+id_card+'&&id_card_des='+id_card_des);
  }
  getSuspectByIDCard(id) {
    return http.get(`/m_society/getidcard/`+id);
  }
  getIDSuspectQuality() {
    return http.get('/m_society/getidcardsuspect');
  }
  getQuality(id) {
    return http.get(`/m_society/`+id);
  }
  createQuality(data) {
    return http.post('/m_society', data);
  }
  updateQuality(id,data) {
    return http.put(`/m_society/${id}`, data);
  }
}

export default new MSocietyDataService();