            <div class="col-sm-4">
              <div class="form-group" style="text-align: left">
                <label for="username">เลขบัตรประชาชนผู้เสียหาย</label>
                <input
                  v-model="id_card"
                  type="number"
                  class="form-control"
                  id="username"
                  placeholder="เลขบัตรประชาชน 13 หลัก"
                />
              </div>
            </div>
            <div class="col-sm-4">
              <div class="form-group" style="text-align: left">
                <label for="username"
                  >วันที่ยื่นคำร้องขอรับการช่วยเหลือเยียวยา
                </label>
                <input id="fill_start_date" class="form-control" />
              </div>
            </div>
            <div class="col-sm-2"></div>
          </div>
        </div>
        <div class="col text-center">
          <button
            @click="search()"
            class="btn btn-primary"
            style="color: white"
          >
            <i class="fas fa-search"></i> ค้นหา
          </button>
        </div>
      </div>
      <div class="row card mt-5" v-if="case_id != 0">
        <div class="card-body p-0">
          <div class="row">
            <div class="col-md-12 mt-3">
              <h5 class="form-group" style="color: #2da3f2; font-weight: bold">
                ข้อมูลผู้เสียหาย
              </h5>
            </div>
            <div class="col-md-3">
              <label>เลขบัตรประชาชนผู้เสียหาย</label>
            </div>
            <div class="col-md-9">
              <label>{{ data.id_card }}</label>
            </div>
            <div class="col-md-3">
              <label>ชื่อ-สกุล</label>
            </div>
            <div class="col-md-9">
              <label>{{ data.firstname }} {{ data.lastname }}</label>
            </div>
          </div>
          <div class="row">
            <div class="col-md-12 mt-3">
              <h5 class="form-group" style="color: #2da3f2; font-weight: bold">
                ข้อมูลลักษณะเหตุการณ์
              </h5>
              <table class="table table-bordered">
                <thead>
                  <tr>
                    <th style="text-align: center" scope="col">
                      ลักษณะเหตุการณ์
                    </th>
                    <th style="text-align: center" scope="col">
                      วันที่ควบคุมตัว
                    </th>
                    <th style="text-align: center" scope="col">
                      วันที่ปล่อยตัว
                    </th>
                    <th style="text-align: center" scope="col">จำนวนวัน</th>
                    <th style="text-align: center" scope="col">
                      หน่วยงานที่ควบคุมตัว
                    </th>
                    <th style="text-align: center" scope="col">สถานที่</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(l, i) in list" :key="i">
                    <td>{{ l.event_name }}</td>
                    <td style="text-align: center">
                      {{ convertdatetothai(l.detained_start_date) }}
                    </td>
                    <td style="text-align: center">
                      {{ convertdatetothai(l.detained_end_date) }}
                    </td>
                    <td style="text-align: center">
                      {{ cal(l.detained_start_date, l.detained_end_date) }} วัน
                    </td>
                    <td style="text-align: center">
                      {{ l.department_detained }}
                    </td>
                    <td style="text-align: center">{{ l.event_id }}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
          <div class="row">
            <div class="col-md-12 mt-3">
              <h5 class="form-group" style="color: #2da3f2; font-weight: bold">
                สิทธิ์ที่ได้รับ
              </h5>
              <table class="table table-bordered mt-3">
                <thead>
                  <tr>
                    <th
                      scope="col"
                      style="background-color: #7bf450; text-align: center"
                    >
                      รายการช่วยเหลือ
                    </th>
                    <th
                      scope="col"
                      style="background-color: #7bf450; text-align: center"
                      v-for="h in headevent"
                      :key="h.event_type_id"
                    >
                      {{ h.event_name }}
                    </th>
                    <th
                      scope="col"
                      style="background-color: #7bf450; text-align: center"
                    >
                      หน่วย
                    </th>
                  </tr>
                </thead>
                <tbody v-if="content.length > 0">
                  <tr v-for="(p, i) in content" :key="i">
                    <th scope="row">{{ p.privileged_name }}</th>
                    <td style="text-align: right" v-if="one">{{ p.one.toLocaleString() }}</td>
                    <td style="text-align: right" v-if="two">{{ p.two.toLocaleString() }}</td>
                    <td style="text-align: right" v-if="three">
                      {{ p.three.toLocaleString() }}
                    </td>
                    <td style="text-align: center">บาท</td>
                  </tr>
                  <tr>
                    <th
                      scope="col"
                      style="background-color: #7bf450; text-align: center"
                    >
                      รวม
                    </th>
                    <th
                      scope="col"
                      style="background-color: #7bf450; text-align: right"
                      v-if="sums.one"
                    >
                      {{ sums.one.toLocaleString() }}
                    </th>
                    <th
                      style="background-color: #7bf450; text-align: right"
                      v-if="sums.two"
                    >
                      {{ sums.two.toLocaleString() }}
                    </th>
                    <th
                      style="background-color: #7bf450; text-align: right"
                      v-if="sums.three"
                    >
                      {{ sums.three.toLocaleString() }}
                    </th>
                    <th style="background-color: #7bf450; text-align: center">
                      บาท
                    </th>
                  </tr>
                </tbody>
              </table>
              <h6 style="font-weight: bold">
                รวมเยียวยาสุทธิ &nbsp;&nbsp;&nbsp;
                {{ numberWithCommas(sums.one + sums.two + sums.three) }}
                &nbsp;&nbsp;&nbsp;บาท
              </h6>
            </div>
          </div>
        </div>
      </div>
      <div class="row card mt-5" v-if="nodata">
        <div class="card-body p-0">
          <div class="row">
            <div class="col-md-12 mt-3">
              <h5
                class="form-group"
                style="text-align: center; color: #2da3f2; font-weight: bold"
              >
                ไม่พบข้อมูล
              </h5>
            </div>
          </div>
