  <script src="https://www.ponnipa.in.th/picker_date.js"></script>

<script>
const customLabels = {
  first: "<<",
  last: ">>",
  previous: "<",
  next: ">",
};

import HomeIcon from "../HomeIcon.vue";
import CasesDataService from "../../services/CasesDataService";
import ConvertService from "../../services/ConvertService";
import CasesEventDataService from "../../services/CasesEventDataService";
import HistoryCaseDataService from "../../services/HistoryCaseDataService";
import CasesPrivilegeDataService from "../../services/CasesPrivilegeDataService";

export default {
  components: {
    HomeIcon,
  },
  data() {
    return {
      name: "HelloWorld",
      case_id: 0,
      id_card: "",
      data: {},
      list: [],
      status: [],
      headevent: [],
      content: [],
      headpri: [],
      sums: {},
      one: false,
      two: false,
      three: false,
      show: true,
      nodata: false,
    };
  },
  mounted() {
    var year = new Date().getFullYear()
    var start = year-2004
    var before = "-"+start+":+0"
    picker_date(document.getElementById("fill_start_date"), {
      year_range: before,
    });
  },
  methods: {
    getEvent() {
      this.headpri = [];
      this.sums = {};
      this.one = false;
      this.two = false;
      (this.three = false),
        CasesPrivilegeDataService.getgroupCasePrivilegeByID(this.case_id).then(
          (res) => {
            this.headevent = res.data;
            // this.content = res.data
            // console.log(res.data);

            for (let p = 0; p < this.headevent.length; p++) {
              if (this.headevent[p].event_type_id == 1) {
                this.one = true;
              }
              if (this.headevent[p].event_type_id == 2) {
                this.two = true;
              }
              if (this.headevent[p].event_type_id == 3) {
                this.three = true;
              }
            }
          }
        );
    },
   getPrivilege() {
      CasesPrivilegeDataService.getCasePrivilegeByID(this.case_id).then(
        (res) => {
          var privil = res.data;
          // this.content = res.data
          // console.log(res.data);
          var event = this.groupBy(privil, "privileged_id");
          // console.log(data[2][0].event_name);
          // console.log(privil);
          for (let e = 0; e < privil.length; e++) {
            this.headpri.push({
              privileged_id: event[e+1][0].privileged_id,
              privileged_name: event[e+1][0].privileged_name,
            });
          }
        }
      );
    },
    getcontent() {
      CasesPrivilegeDataService.getCasePrivilegeByID(this.case_id).then(
        (res) => {
          var privil = res.data;
          // console.log(res.data);
          for (let p = 0; p < this.headpri.length; p++) {
            for (let e = 0; e < this.headevent.length; e++) {
              for (let d = 0; d < privil.length; d++) {
                if (
                  privil[d].privileged_id == this.headpri[p].privileged_id &&
                  privil[d].event_type_id == this.headevent[e].event_type_id
                ) {
                  if (privil[d].event_type_id == 1) {
                    this.headpri[p].one = privil[d].amount;
                    this.headpri[p].oneid = privil[d].cases_privileged_id;
                  } else if (privil[d].event_type_id == 2) {
                    this.headpri[p].twoid = privil[d].cases_privileged_id;
                    this.headpri[p].two = privil[d].amount;
                  } else if (privil[d].event_type_id == 3) {
                    this.headpri[p].threeid = privil[d].cases_privileged_id;
                    this.headpri[p].three = privil[d].amount;
                  }
                }
              }
            }
          }
          this.content = this.headpri;
          // console.log(this.content);
          var one = 0;
          var two = 0;
          var three = 0;
          for (let p = 0; p < this.content.length; p++) {
            // console.log(this.content[p].one);
            if (isNaN(this.content[p].one)) {
              this.content[p].one = 0;
            }
            if (isNaN(this.content[p].two)) {
              this.content[p].two = 0;
            }
            if (isNaN(this.content[p].three)) {
              this.content[p].three = 0;
            }
            one = one + parseInt( this.content[p].one);
            two = two + parseInt(this.content[p].two);
            three = three + parseInt(this.content[p].three);
          }
          this.sums = {
            one: one,
            two: two,
            three: three,
          };
          // console.log(this.sums);
        }
      );
    },
    groupBy(array, key) {
      const result = {};
      array.forEach((item) => {
        if (!result[item[key]]) {
          result[item[key]] = [];
        }
        result[item[key]].push(item);
      });
      return result;
    },
    numberWithCommas(x) {
      var r = 0;
      x = parseInt(x);
      if (isNaN(x)) {
        r = "";
      } else if (x == 0) {
        r = 0;
      } else {
        r = x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      }
      return r;
    },
    getstatus() {
      HistoryCaseDataService.getHistoryCaseByCaseID(this.case_id).then(
        (res) => {
          this.status = res.data;
          // console.log(res.data);
        }
      );
    },
    cal(start, end) {
      var date1 = new Date(start);
      var date2 = new Date(end);
      var diffTime = date2.getTime() - date1.getTime();
      var diffDay = diffTime / (1000 * 3600 * 24);
      return diffDay;
    },
    getEventByCaseID() {
      CasesEventDataService.getEventByCaseID(this.case_id).then((res) => {
        this.list = res.data;
        // console.log(res.data);
      });
    },
    back() {
      this.$router.push("/home");
    },
    onChangePage(pageOfItems) {
      // update page of items
      this.pageOfItems = pageOfItems;
    },
    convertdatetothai(datetimes) {
      if (datetimes) {
        const date = new Date(datetimes);
        const result = date.toLocaleDateString("th-TH", {
          year: "numeric",
          month: "short",
          day: "numeric",
        });
        return result;
      } else {
        return "-";
      }
    },
    hasHistory() {
      return window.history.length > 2;
    },
    search() {
      this.case_id = 0;
      if (this.id_card == "") {
        alert("กรุณากรอกเลขบัตรประชาชนผู้เสียหาย");
      } else if (document.getElementById("fill_start_date").value == "") {
        alert("กรุณากรอกวันที่ยื่นคำร้องขอรับการช่วยเหลือเยียวยา");
      } else {
        var da;
        var d = document.getElementById("fill_start_date").value;
        // var dd = new Date(d)
        if (d != "") {
          da = ConvertService.createddate(d);
        } else if (d == "") {
          da = "";
        }
        // if (this.id_card == '') {
        //   this.id_card = null
        // }
        // if (this.status_id == '') {
        //   this.status_id = null
        // }
        // console.log(this.id_card);
        // console.log(da);
        CasesDataService.getCaseSuspect(this.id_card, da).then((res) => {
          // console.log(res.data);
          if (res.data.length == 0) {
            this.nodata = true;
          } else {
            this.nodata = false;
            this.case_id = res.data.case_id;
            this.data = res.data;
            this.getEventByCaseID();
            this.getstatus();
            this.getEvent();
            this.getPrivilege();
            this.getcontent();
          }
          // console.log(this.case_id);
        });
      }
    },
  },

  computed: {
    currentUser() {
      return this.$store.state.auth.user;
    },
  },
};
</script>

